
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
;
import EssayHeader from './EssayHeader';

export default {
  data() {
    return {
      swiperItemIndex: 0,
    };
  },
  components: {
    Swiper,
    SwiperItem,
    EssayHeader,
  },
  props: {
    photos: {
      type: Array,
      default: [],
    },
    title: String,
    authorName: String,
  },
  computed: {
    numberIndicator() {
      return this.photos && `${this.swiperItemIndex + 1}/${this.photos.length}`;
    },
  },
  created() {
    // 
    function check(delay) {
      const imgs = document.querySelectorAll('.movie-swiper img');
      const timer = setTimeout(check, delay);
      if (imgs.length > 0) {
        clearTimeout(timer);
      }
      imgs.forEach((img) => {
        if (img.getBoundingClientRect().height < window.innerWidth) {
          // img.classList.add('img');
          img.style.width = window.innerWidth;
          img.style.height = 'auto';
        }
      });
    }
    check(200);
  },
};



