import { mapState, mapMutations } from 'vuex';

export default{
  computed: {
    ...mapState({
      playState: state => state.music.playState,
    }),
  },
  methods: {
    ...mapMutations(['updateShowMusicPlayer']),
    show() {
      this.updateShowMusicPlayer({ show: true });
    },
  },
};



