import { mapMutations } from 'vuex';

export default {
  props: {
    leftOptions: {
      type: Object,
      default() {
        return {
          showBack: false,
          preventGoBack: false,
        };
      },
    },
    title: {
      type: String,
      default() {
        return '';
      },
    },
    transition: String,
    rightOptions: {
      type: Object,
      default() {
        return {
          showMore: false,
        };
      },
    },
  },
  methods: {
    ...mapMutations(['updateDirection']),
    onClickBack() {
      this.updateDirection({ direction: 'back' });
      if (this.leftOptions.preventGoBack) {
        this.$emit('on-click-back');
      } else if (this.$router) {
        this.$router.back();
      } else {
        window.history.back();
      }
    },
  },
};



