import { Toast } from 'mint-ui';
import { mapState } from 'vuex';

export default{
  components: {
    Toast,
  },
  props: ['operation', 'cmtid', 'type', 'username', 'content', 'itemid'],
  data() {
    return {
      wordNum: 1000,
    };
  },
  computed: {
    ...mapState({
      host: state => state.one.host,
      basicQueryString: state => state.one.basicQueryString,
      jwt: state => state.one.jwt,
      userId: state => state.one.userId,
    }),
  },
  methods: {
    onChange() {
      this.wordNum = 1000 - this.$refs.input.value.length;
    },
    cancel() {
      this.$emit('cancel');
    },
    async submit() {
      const input = this.$refs.input.value.trim();
      if (input.length === 0) {
        Toast({
          message: ':-D',
          position: 'center',
          duration: 1400,
        });
      } else {
        this.$emit('cancel');
        const form = {
          itemid: this.itemid,
          cmtid: this.cmtid,
          jwt: this.jwt,
          type: this.type,
          content: input,
        };
        try {
          const resp = await this.$http.post(`/comment/add?${this.basicQueryString}`, form);
          const result = resp.data;
          if (result.res === 0) {
            Toast({
              message: `${this.operation === 'publish' ? '' : ''}:-D`,
              position: 'bottom',
              duration: 1400,
            });
          } else {
            Toast({
              message: result.msg,
              position: 'bottom',
              duration: 1400,
            });
          }
        } catch (err) {
          Toast({
            message: '',
            position: 'bottom',
            duration: 1400,
          });
        }
      }
    },
  },
};



