export default {
  props: {
    tag: String,
    related: Object,
  },
  computed: {
    author() {
      switch (this.tag) {
        case '':
          return this.related.answer_title;
        default:
          return `${this.related.author[0].user_name}`;
      }
    },
    title() {
      switch (this.tag) {
        case '':
          return this.related.question_title;
        default:
          return this.related.hp_title;
      }
    },
  },
  methods: {
    onClick() {
      let id;
      switch (this.tag) {
        case '':
          id = this.related.question_id;
          break;
        default:
          id = this.related.content_id;
      }
      this.$emit('on-clicke-item', id);
    },
  },
};



