import HeaderBar from './HeaderBar';

export default{
  components: {
    HeaderBar,
  },
  data() {
    return {
      leftOptions: {
        showBack: true,
      },
    };
  },
  methods: {
    search(event) {
      const searchString = event.target.value;
      this.$router.push({ path: `/search/${searchString}` });
    },
  },
  mounted() {
    const self = this;
    setTimeout(() => {
      self.$refs.input.focus();
    }, 600);
  },
};



